<template>
  <div style="width: 90vw">
    <div class="containerr">
      <NavBar2/>
    <div style="height: 70px; width:100%"></div>
      <div class="head-detail">
        <div class="back-detail">
          <img
            src="https://cdn-icons-png.flaticon.com/512/860/860790.png?w=740&t=st=1685281452~exp=1685282052~hmac=ce43c00291cf7b56d74b446ac5442e73dffce5ebecaf4d187845d164d5d70992"
            width="15px"
            height="15px"
          /><a href="/cassava/diseasevue">กลับ</a>
        </div>
        <div class="threeheader-detail">
          <a style="color: #68a4c4">โรคมันสำปะหลัง</a>
          <p>{{ disease.name }}</p>
          <div class="threeheader-line-detail"></div>
        </div>
      </div>
      <div class="des">
          <div class="showimage">
            <carousel @next="next" @prev="prev" v-if="disease.image.length>1">
                <carousel-slide v-for="(slide,index) in disease.image" :key="slide" :index="index" :visibleSlide="visibleSlide" :direction="direction">
                    <div style="height: 300px; width:450px"><img :src="'data:image/png;base64,' + slide" style="width: 100%;" /></div>
                </carousel-slide>
            </carousel>
            <div style="height: 300px; width:520px" v-else-if="disease.image.length==1"><img :src="'data:image/png;base64,' + disease.image[0]" style="height: 100%;" /></div>
            <div style="height: 300px; width:520px" v-else><img :src="getResourceImagePath('/img/no-image.jpg')" alt="Example Image" style="height: 100%;"></div>
        </div>
        <div class="detail">
          <div class="detail-box1">
            <div class="detail-name"><a>รายละเอียดโรค</a></div>
            <div class="detail-text">
              <div class="detail-left1">
                <ul>
                  <li>ชื่อโรค</li>
                  <li>ลักษณะอาการ</li>
                  <li>การควบคุมโรค</li>
                  <li>การควบคุมแมลงพาหะ</li>
                </ul>
              </div>
              <div class="detail-right1">
                <ul>
                  <li>โรคแอนแทรคโนส</li>
                  <li>ใบเหลือง</li>
                  <li>
                    ใช้ท่อนพันธุ์มันสำปะหลังที่ปลอดโรค (Ikotun and Hanh, 1991)
                  </li>
                  <li>-</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="detail-box1">
            <div class="detail-name"><a>แมลงพาหะ</a></div>
            <div class="detail-text">
              <div class="detail-leftone">
                <ul>
                  <li>ไรแดงหม่อน (Tetranychus truncatus)</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="detail-box1">
            <div class="detail-name"><a>เชื้อสาเหตุ</a></div>
            <div class="detail-text">
              <div class="detail-leftone">
                <ul>
                  <li>Sri Lankan cassava mosaic virus (เชื้อไวรัส)</li>
                  <li>Candidatus Phytoplasma (เชื้อไฟโตพลาสมา)</li>
                  <li>Xanthomnas axonopodis pv. manihotis (แบคทีเรีย)</li>
                  <li>
                    Colletotrichum gloeosporioides f.sp. manihotis (เชื้อรา)
                  </li>
                </ul>
